}

.verifytext{
  font-size: 0.8em;
  text-align: center;
}

.navlogo {
  height:100%;
  width:3em;
}

.navhover {
  background-image: linear-gradient(
    to right,
    #F44336 ,
    #F44336  50%,
    #4e4c4c 50%
  );
  background-size: 200% 100%;
  background-position: -100%;
  /* display: inline-block; */
  /* padding: 5px 0; */
  position: relative;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease;
  font-size:0.95rem;
  white-space: nowrap;
}

.navhover:before{
  content: '';
  background: #ee210a;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
  transition: all 0.3s ease-in;
}

.navhover:hover {
 background-position: 0;
 cursor: pointer;
 font-size: 1.2rem;
}

.navhover:hover::before{
  width: 100%;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.text-shadow {
  text-shadow: 0px 10px 12px #b3b3b3;
}

.title-color
{background: linear-gradient(90deg, #C93226 0%, #0015f7 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
}

.title{
  font-weight: bolder;
  font-size: 2em;
  color:#f56241 ;
  white-space: nowrap;
}


.typewriter {
  color: #ff0000;
  font-family: monospace;
  font-size: 1.6em;
  overflow: hidden;
  border-right: .15em solid orange;
  white-space: nowrap;
  margin: 0 auto;
  margin-bottom: 1em;
  letter-spacing: .08em;
  animation: 
    typing 6s steps(30, end) infinite,
    blink-caret .5s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  0% { width: 0;} 
  50% { width: 100% }
  80% { width: 100% }
  100% { width: 0 }
}

.typedtext:after {
  content:"";
  animation: textchange 12s infinite;
}

@keyframes textchange {
  0%{content:"Frontend Web Dev:"}
  50%{content:"Frontend Web Dev:"}
  51%{content:"Medical Doctor"}
  100%{content:"Medical Doctor"}
}
/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange }
}

.pjbox{
  display: flex;
  flex-direction: row;
  justify-content: center;
