  width: 15vmin;
}

@keyframes rotating {
  from{
      transform: rotate(0deg);
  }
  to{
      transform: rotate(360deg);
  }
}
.rotating{
  animation: rotating 15s linear infinite;
}

.skillset-text{
  padding-inline: 20%;
  text-size-adjust: 0.8em;
  text-align: justify;
}

@keyframes rotatetest {
  0%{
    transform : rotate(-5deg);
  }
  50%{
    transform: rotate(5deg);
  }
  100%{
    transform : rotate(-5deg);
  }
}

.test:hover {
  animation: rotatetest 0.8s ease infinite;
  cursor: pointer;
}

